// Import
@import url('https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@400;700;900&family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

// Variables
$primary-color: #000000;
$primary-color2: #F2F2F2;
$top-menu-height: 43px;

@mixin fdja {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

;

@mixin fdj {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

;

@mixin fda {
    display: flex;
    flex-direction: column;
    align-items: center
}

;

@mixin fja {
    display: flex;
    justify-content: center;
    align-items: center;
}

;

@mixin fj {
    display: flex;
    justify-content: center;
}

;

@mixin fa {
    display: flex;
    align-items: center
}

;

@mixin fd {
    display: flex;
    flex-direction: column;
}

// General
* {
    font-family: 'Montserrat', "Cinzel Decorative";
}

::-webkit-scrollbar {
    width: 2px;
}

// .App {
//     text-align: center;
//     color: $primary-color;
//     background-color: $primary-color2;
// }
h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
    margin: 0px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Cinzel Decorative";
}

p {
    font-family: "montserrat";
}

a {
    text-decoration: none;
}

// Components
// Components - Top Bar
.top-bar {
    @include fa;
    justify-content: space-between;
    align-self: center;
    width: calc(100dvw - (2 * $top-menu-height));
    height: $top-menu-height;
    position: absolute;
    top: $top-menu-height;

    h1 {
        color: $primary-color2;
        font-weight: 400;
    }

    .menu-btn {
        z-index: 900;
        height: 22.2px;
        width: 40px;
        @include fda;
        justify-content: space-between;
        .line {
            width: 100%;
            height: 10%;
            background-color: $primary-color2;
        }
    }
}
// Components - Side Bar
.side-bar{
    @include fja;
    width: 100dvw;
    height: 100dvh;
    background-color: #171717;
    position: absolute;
    right: -100%;
    transition: right 0.6s ease-out;
    .container{
        width: 85%;
        height: 90%;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        .left,
        .right{
            width: 100%;
            height: 100%;
        }
    }
}

// Pages
// Pages - Landing Page
.landing-banner {
    @include fd;
    width: 100dvw;
    height: 100dvh;
    background-image: url(./images/products/landingbannerimage.jpg);
    background-size: cover;
    background-position: bottom;
    filter: grayscale(1);
    overflow-x: hidden;
    .filter {
        height: 100%;
        width: 100%;
        background: linear-gradient(rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0) 100%);
    }
}

.brand-info-section {
    // height: 130vh;
    height: 1080px;
    width: 100dvw;
    position: relative;

    .product-1 {
        height: 58%;
        width: 38%;
        object-fit: cover;
        position: relative;
        top: 50px;
        filter: brightness(40%);
    }

    .product-2 {
        height: 39%;
        width: 51%;
        object-fit: cover;
        position: absolute;
        bottom: 50px;
        right: 0px;
        filter: grayscale(1);
    }

    .texts {
        @include fd;
        gap: 22px;
        width: 404px;
        height: 346px;
        position: absolute;
        top: 20%;
        left: 45%;
    }
}

.landing-page-product-1,
.landing-page-product-3 {
    height: 80vh;
    width: 100dvw;
    @include fa;
    gap: 50px;

    .left {
        @include fdj;
        align-items: flex-end;
        width: 70%;
        color: #F2F2F2;
        margin: 0px 0px 0px 40px;

        p {
            text-align: end;
        }
    }

    .right {
        position: relative;
        right: 0px;
        width: 100%;
        height: calc(100% - 100px);

        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
            filter: brightness(60%);
        }
    }
}

.landing-page-product-1 {
    background-color: #171717;
}

.landing-page-product-2 {
    @include fa;
    height: 100dvh;

    .filter {
        width: 100%;
        height: 100%;
        background-image: url(./images/products/product1-2.jpg);
        background-size: cover;
        background-position: center;
        filter: brightness(50%);
    }

    .text {
        @include fd;
        width: 404px;
        position: absolute;
        left: 70px;
        color: #F2F2F2;
        gap: 22px;
    }
}

.landing-page-product-3 {
    .left {
        color: #171717;
    }
}

.ordinary-challenge {
    position: relative;
    height: 1000px;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 1fr);

    .left,
    .right {
        height: 100%;
        width: 100%;
    }

    .left {
        @include fd;
        justify-content: flex-end;
        gap: 50px;
        padding: 0px 0px 60px 0px;
        height: calc(100% - 60px);

        .image {
            height: 360px;
            width: 80%;

            img {
                height: 100%;
                width: 100%;
                object-fit: cover;
                filter: grayscale(.95);
            }
        }

        .texts {
            @include fd;
            gap: 22px;
            position: relative;
            left: 40px;
            width: 62%;
            align-items: flex-end;
            text-align: end;
        }
    }

    .right {
        background-image: url(./images/products/product5-1.jpg);
        background-size: cover;
        background-position: center;
    }
}