@import url("https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@400;700;900&family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");
* {
  font-family: "Montserrat", "Cinzel Decorative";
}

::-webkit-scrollbar {
  width: 2px;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
  margin: 0px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Cinzel Decorative";
}

p {
  font-family: "montserrat";
}

a {
  text-decoration: none;
}

.top-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-self: center;
  width: calc(100dvw - 86px);
  height: 43px;
  position: absolute;
  top: 43px;
}
.top-bar h1 {
  color: #F2F2F2;
  font-weight: 400;
}
.top-bar .menu-btn {
  z-index: 900;
  height: 22.2px;
  width: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.top-bar .menu-btn .line {
  width: 100%;
  height: 10%;
  background-color: #F2F2F2;
}

.side-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100dvw;
  height: 100dvh;
  background-color: #171717;
  position: absolute;
  right: -100%;
  transition: right 0.6s ease-out;
}
.side-bar .container {
  width: 85%;
  height: 90%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.side-bar .container .left,
.side-bar .container .right {
  width: 100%;
  height: 100%;
}

.landing-banner {
  display: flex;
  flex-direction: column;
  width: 100dvw;
  height: 100dvh;
  background-image: url(./images/products/landingbannerimage.jpg);
  background-size: cover;
  background-position: bottom;
  filter: grayscale(1);
  overflow-x: hidden;
}
.landing-banner .filter {
  height: 100%;
  width: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0) 100%);
}

.brand-info-section {
  height: 1080px;
  width: 100dvw;
  position: relative;
}
.brand-info-section .product-1 {
  height: 58%;
  width: 38%;
  object-fit: cover;
  position: relative;
  top: 50px;
  filter: brightness(40%);
}
.brand-info-section .product-2 {
  height: 39%;
  width: 51%;
  object-fit: cover;
  position: absolute;
  bottom: 50px;
  right: 0px;
  filter: grayscale(1);
}
.brand-info-section .texts {
  display: flex;
  flex-direction: column;
  gap: 22px;
  width: 404px;
  height: 346px;
  position: absolute;
  top: 20%;
  left: 45%;
}

.landing-page-product-1,
.landing-page-product-3 {
  height: 80vh;
  width: 100dvw;
  display: flex;
  align-items: center;
  gap: 50px;
}
.landing-page-product-1 .left,
.landing-page-product-3 .left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  width: 70%;
  color: #F2F2F2;
  margin: 0px 0px 0px 40px;
}
.landing-page-product-1 .left p,
.landing-page-product-3 .left p {
  text-align: end;
}
.landing-page-product-1 .right,
.landing-page-product-3 .right {
  position: relative;
  right: 0px;
  width: 100%;
  height: calc(100% - 100px);
}
.landing-page-product-1 .right img,
.landing-page-product-3 .right img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  filter: brightness(60%);
}

.landing-page-product-1 {
  background-color: #171717;
}

.landing-page-product-2 {
  display: flex;
  align-items: center;
  height: 100dvh;
}
.landing-page-product-2 .filter {
  width: 100%;
  height: 100%;
  background-image: url(./images/products/product1-2.jpg);
  background-size: cover;
  background-position: center;
  filter: brightness(50%);
}
.landing-page-product-2 .text {
  display: flex;
  flex-direction: column;
  width: 404px;
  position: absolute;
  left: 70px;
  color: #F2F2F2;
  gap: 22px;
}

.landing-page-product-3 .left {
  color: #171717;
}

.ordinary-challenge {
  position: relative;
  height: 1000px;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.ordinary-challenge .left,
.ordinary-challenge .right {
  height: 100%;
  width: 100%;
}
.ordinary-challenge .left {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 50px;
  padding: 0px 0px 60px 0px;
  height: calc(100% - 60px);
}
.ordinary-challenge .left .image {
  height: 360px;
  width: 80%;
}
.ordinary-challenge .left .image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  filter: grayscale(0.95);
}
.ordinary-challenge .left .texts {
  display: flex;
  flex-direction: column;
  gap: 22px;
  position: relative;
  left: 40px;
  width: 62%;
  align-items: flex-end;
  text-align: end;
}
.ordinary-challenge .right {
  background-image: url(./images/products/product5-1.jpg);
  background-size: cover;
  background-position: center;
}


